.partners {
  background-color: var(--color-athens);
  padding-top: 80px;
  padding-bottom: 80px;

  &--services {
    padding-top: 0;
    padding-bottom: 117px;
  }

  &--services-v2 {
    padding-top: 0px;
    padding-bottom: 117px;
  }

  &--work {
    background-color: var(--color-light);
    padding-top: 183px;
    padding-bottom: 114px;
  }

  &--about {
    padding-top: 120px;
    background-color: var(--color-light);
  }

  &__title {
    margin-bottom: 59px;

    &--about {
      margin-bottom: 23px;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 102px;

    &--about {
      grid-template-rows: repeat(2, 1fr);
      row-gap: 37px;
    }
  }
}
