.footer {
  padding-top: 78px;

  background-color: var(--color-ebony);

  color: var(--color-light);
  padding-bottom: 73px;

  &__container {
    grid-template-rows: repeat(2, 1fr);
    gap: 29px;
  }

  &__left {
    grid-column: 7 span;
    max-width: 493px;
  }

  &__top {
    @include flex-v-center;
    align-items: flex-start;

    margin-bottom: 21px;
  }

  &__form {
    grid-column: 5 span;
  }

  &__address {
    grid-column: 4 span;
  }

  &__nav {
    grid-column: 8 span;
  }

  &__copyright {
    grid-column: 12 span;
  }

  &__logo {
    margin-right: 59px;
  }

  &__descr {
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;

    color: var(--color-light);

    opacity: 0.6;
  }
}

.footer-form {
  margin-bottom: 21px;

  &__suptitle {
    font-weight: 700;
    font-size: 24px;
    line-height: 150%;

    color: var(--color-light);

    margin-bottom: 24px;
  }

  &__items {
    @include flex-v-center;
    margin-bottom: 16px;
  }

  .form-field {
    margin: 0;
    width: 364px;
  }

  .form-field__input {
    border-radius: 4px;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    border: 1px solid rgba(255, 255, 255, 0.2);
    height: 44px;

    background-color: rgba(255, 255, 255, 0.12);

    color: var(--color-light);
    opacity: 0.6;
  }

  &__btn {
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    padding: 11.7px 21px;
    height: 44px;
    letter-spacing: normal;
  }

  &__descr {
    font-size: 12px;
    line-height: 150%;

    color: var(--color-light);

    opacity: 0.6;
  }
}

.footer-address {
  @include flex-collumn-start;

  &__item {
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;

    color: rgba(255, 255, 255, 0.6);

    &:not(:last-child) {
      margin-bottom: 4px;
    }
  }

  &__name {
    color: var(--color-light);
  }

  &__link {
    color: inherit;
  }
}

.footer-title {
  font-weight: 700;
  font-size: 16px;
  line-height: 150%;

  letter-spacing: 1px;
  text-transform: uppercase;

  color: var(--color-light);

  margin-bottom: 13px;
}

.footer-nav {
  display: flex;
  padding-left: 54px;

  &__col {
    &:not(:last-child) {
      margin-right: 125px;
    }
  }

  &__item {
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;

    color: rgba(255, 255, 255, 0.6);

    &:not(:last-child) {
      margin-bottom: 4px;
    }

    a {
      color: inherit;
    }
  }
}

.footer-copyright {
  margin-top: -25px;
  font-weight: 400;
  font-size: 12px;
  line-height: 150%;

  color: var(--color-light);

  &__link {
    position: relative;
    display: inline-block;
    padding-left: 24px;

    color: inherit;
    font-size: inherit;

    &::before {
      content: "";
      position: absolute;
      width: 16px;
      height: 16px;
      top: 0;
      left: 2px;

      background-image: url("../img/footer-heart.svg");
    }
  }
}
