.team {
  background-color: var(--color-light);
  padding-top: 97px;
  padding-bottom: 120px;

  background-image: url("../img/about/team-bg.png");
  background-repeat: no-repeat;
  background-position: left -9px bottom -319px;

  &__list {
    row-gap: 60px;
    margin-bottom: 80px;
  }

  &__item {
    grid-column: 3 span;
  }

  &__image {
    border-radius: 4px;
    position: relative;
    overflow: hidden;

    margin-bottom: 16px;

    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    &:hover {
      .team-social {
        visibility: visible;
        height: 60px;
      }
    }
  }

  &__author {
    display: block;
    font-weight: 700;
    font-size: 20px;
    line-height: 150%;

    text-align: center;

    color: var(--color-gray);
    margin-bottom: 5px;
  }

  &__post {
    display: block;
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;

    text-align: center;

    color: var(--color-storm);
  }

  &__positions {
    font-weight: 400;
    font-size: 18px;
    line-height: 150%;

    text-align: center;

    color: var(--color-ebony);

    a {
      font-size: 18px;
      line-height: 150%;

      text-align: center;
      text-decoration-line: underline;

      color: var(--color-primary);
      padding-left: 2px;
    }
  }
}

.team-social {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 5;

  background-color: rgba(#ff5a30, 0.5);

  visibility: hidden;
  height: 0;

  transition: visibility 0.3s ease-in-out, height 0.3s ease-in-out;

  &__list {
    @include flex-all-center;
  }

  &__item {
    padding: 20px 0;

    &:not(:last-child) {
      margin-right: 12px;
    }
  }

  &__icon {
    width: 20px;
    height: 20px;
    object-fit: cover;
    fill: var(--color-light);
  }
}
