.page-hero {
  padding-top: 153px;
  background-color: var(--color-athens);

  padding-bottom: 80px;
  min-height: 600px;

  background-position: right -10px top;
  background-repeat: no-repeat;

  &__title {
    margin-bottom: 23px;

    max-width: 600px;
    font-weight: 700;
    font-size: 72px;
    line-height: 130%;
    letter-spacing: 1px;
    text-transform: uppercase;

    color: var(--color-ebony);

    &--small {
      font-weight: 700;
      font-size: 64px;
      line-height: 120%;
      letter-spacing: 1px;
      text-transform: initial;
    }

    &--mb {
      margin-bottom: 60px;
    }
  }

  &__descr {
    max-width: 600px;
    font-weight: 400;
    font-size: 20px;
    line-height: 150%;

    color: var(--color-storm);
  }

  &--services {
    background-image: url("../img/bg-image-services.png");
  }

  &--services-inner {
    background-image: url("../img/services-v2/bg-hero-services-v2.png");
  }

  &--work {
    background-image: url("../img/work/hero-work-bg.png");
  }

  &--work-inner {
    min-height: auto;
    background-image: url("../img/work-v2/bg-hero-work-v2.png");
    background-position: right -10px top 48px;
    background-color: var(--color-light);
    padding-bottom: 100px;
  }

  &--about {
    position: relative;
    background-image: url("../img/about/about-hero-image-1.png");
  }

  &--positions {
    background-image: url("../img/positions/hero-bg-positions.png");
    background-position: right -10px bottom;
  }

  &__image {
    position: absolute;
    top: 0;
    right: -9px;
    z-index: 2;
  }
}
