.work-images {
  overflow: hidden;

  .swiper-slide img {
    display: block;
    border-radius: 4px;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &-slider {
    position: relative;
    margin-bottom: 20px;
  }
}

.work-images-nav {
  overflow: hidden;

  .swiper-slide {
    border-radius: 4px;
    opacity: 0.6;
    transition: 0.3s opacity ease-in-out;
    cursor: pointer;
  }

  .swiper-slide-thumb-active {
    opacity: 1;
  }

  .swiper-slide img {
    display: block;
    border-radius: 4px;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.work-images {
  &__slider-nav {
    position: absolute;
    justify-content: space-between;
    width: 100%;
    top: 50%;
    transform: translateY(-50%);
    z-index: 15;
    padding: 0 30px;
  }
}
