.swiper {
  margin: -100px !important;
  padding: 100px !important;
}

.projects-section {
  padding-bottom: 120px;
  background-color: var(--color-light);

  &__top {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;

    margin-bottom: 48px;
  }

  &__learn {
    margin-top: 80px;
  }

  &__title {
    margin: 0;
    max-width: 705px;

    font-weight: 700;
    font-size: 46px;
    line-height: 130%;

    color: var(--color-ebony);
    padding-bottom: 11px;
  }

  &__slider-nav {
    transform: translateX(12px);
  }

  &__items {
    margin: -100px;
    padding: 100px;

    .swiper-slide {
      opacity: 0;
      visibility: hidden;

      transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
    }

    .slide-visible {
      opacity: 1;
      visibility: visible;
    }
  }

  // For servisec-v2 section

  &--services {
    padding-top: 154px;
    padding-bottom: 99px;
  }

  // For work-v2 section

  &--work-inner {
    padding-top: 184px;
  }
}

.projects-item {
  display: flex;
  flex-direction: column;
  border-radius: 4px;

  box-shadow: 0px 80px 80px -20px rgba(154, 156, 165, 0.08),
    0px 30px 24px -10px rgba(154, 156, 165, 0.05),
    0px 12px 10px -6px rgba(154, 156, 165, 0.04),
    0px 4px 4px -4px rgba(30, 33, 44, 0.03);

  background-color: var(--color-light);

  padding-bottom: 24px;

  &__image {
    display: block;
    width: 100%;
    height: 100%;

    object-fit: cover;

    margin-bottom: 16px;

    transition: height 0.3s ease-in-out;
  }

  &__content {
    @include flex-collumn-centr;
  }

  &__title {
    font-weight: 700;
    font-size: 20px;
    line-height: 150%;

    color: var(--color-ebony);

    margin-bottom: 4px;
  }

  &__descr {
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;

    color: var(--color-storm);
  }

  &__btn {
    margin-top: 24px;
    display: none;
  }
}
