.header {
  padding-top: 23px;
  padding-bottom: 21px;

  &__container {
    @include flex-all-sb;
  }

  &--main {
    color: var(--color-light);
  }

  &__nav {
    margin-left: -112px;
  }

  &--secondary {
    width: 100%;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 5;
  }
}
